.breathing-circle {
        position: absolute;
		width: 200px;
        height: 200px;
        border-radius: 50%;
        margin: 20px;
        top: 300px;
        left: 40%;
        z-index: 100;
		/*background-image: linear-gradient(to left,lightgrey,gold);*/
		background-color: transparent;
		border: 2px solid white;
		text-align: center;
		line-height: 50px;
		font-size: 18px;
		text-transform: capitalize;
		-webkit-animation:brath 12s  linear infinite;
		        animation:brath 12s  linear infinite;
	}
	@-webkit-keyframes brath{
        0%{transform: scale(0.3);}
        16%{transform: scale(.5);}
        32%{transform: scale(.8);}
		50%{transform: scale(1);}
		75%{transform: scale(.5);}
		100%{transform: scale(0.3);}
	}
	@keyframes brath{
        0%{transform: scale(0.3);}
        16%{transform: scale(.5);}
        32%{transform: scale(.8);}
		50%{transform: scale(1);}
		75%{transform: scale(.5);}
		100%{transform: scale(0.3);}
	}





