/* SIGNUP FORM*/

.signup-visual {
    width: auto;
    width: 100vh;
    margin-left: 0;

  }
  
  .image-signup-container{
    margin-left: 0;
    margin-right: 0;
 
  }

  input {
    padding: 8px;
    padding-left: 10px;
    border-radius: 10px;
    border-color: black;
    text-align: center;
    color: black;
  }

  button {
    padding: 8px;
    padding-right: 20px;
    padding-left: 20px;
  }
  
  .alignment-items {
    display: flex;
    flex-direction: row;
  }
  .signup-col {
    padding: 0;
    margin-right: 0;
    margin-left: 0;
  }

  .signup-form-container {
    margin: 0;
    padding: 0;
  }